import React from 'react';
import Auxiliary from '../../hoc/Auxiliary';
import Button from '../../components/UI/Button/Button';
import LandingBox from '../../components/UI/LandingBox/LandingBox';
import classes from './landingpage.module.css';
import collect from '../../assets/collect.png';
import save from '../../assets/save.png';
import investigate from '../../assets/investigate.png';
import cycle from '../../assets/cycle.png';
import calculate from '../../assets/calculate.png';
import { connect } from 'react-redux';

const DATA = {
            0 : {title: 'zbierz informacje...',
                text: ' ... o procesie pracy na danym stanowisku. We pod uwag: urzdzenia i maszyny, substancje chemiczne, lokalizacj stanowiska pracy, pomiary rodowiska pracy itp.',
                img: collect},
            1 : {title: 'zidentyfikuj zagroenia',
                text: '.... jakie wystpuj na podstawie zebranych informacji. Pomoe Ci w tym lista kontrolna znajdujca si w formularzu oceny ryzyka zawodowego.',
                img: investigate},
            2 : {title: 'oszacuj ryzyko',
                text: 'Okrel potencjalne skutki wystpienia niepodanego zdarzenia oraz prawdopodobiestwo jego wystpienia. Na tej podstawie oszacowane zostanie ryzyko przy uyciu metody piciostopniowej wg polskiej normy PN-N-18002',
                img: calculate},
            3 :{title: 'zapisz dokument',
                text: 'Ocena ryzyka bdzie natychmiast dostpna dla niezalogowanych uytkownikw w Panelu Uytkownika. Wystarczy, e poinformujesz ich o nowym dokumencie.',
                img: save},
            4: {title: 'monitoruj, przegldaj, aktualizuj',
                text: 'Z poziomu Panelu Administratora na bieco moesz monitorowa dokumenty do przegldu i aktualizowa w zalenoci od potrzeby ( dodatkowy panel boczny po otwarciu danej oceny ryzyka zawodowego )',
                img: cycle}
    }

const landingpage = (props) => {

    const box = Object.keys(DATA).map(box => {
        return <LandingBox title = {DATA[box].title}
                            text = {DATA[box].text}
                            img = {DATA[box].img}
                            right = {box %2 === 0 ? true : false}
                            key = {box}
                />
    })

    let redirectBox = (<Auxiliary>
                            <h3>Zaloguj lub zarejestruj si, aby mc korzysta:</h3>
                            <Button btnType = 'SubmitFocus' clicked = {() => props.history.push('/authentication')}>Zaloguj si / Zarejestruj si</Button>
                        </Auxiliary>);
    if (props.isAuth) {
        redirectBox = (<Auxiliary>
                            <h3>Przejd do panelu administratora</h3>
                            <Button btnType = 'SubmitFocus' clicked = {() => props.history.push('/')}>Panel administratora</Button>
                        </Auxiliary>)
    }
    
    
    return (
        <div className={classes.LandingPage}>
            <div className={classes.Banner}>
                <h1>Ryzyko zawodowe</h1>
                <p>to prawdopodobiestwo wystpienia niepodanych zdarze zwizanych z wykonywan prac, powodujcych straty, w szczeglnoci wystpienia u pracownikw niekorzystnych skutkw zdrowotnych w wyniku zagroe zawodowych wystpujcych w rodowisku pracy lub sposobu wykonywania pracy</p>
                <p> 2 pkt 7 Rozporzdzenia Ministra Pracy i Polityki Socjalnej z dnia 26 wrzenia 1997 r. w sprawie oglnych przepisw bhp, z pn. zm.(t.j.Dz.U. 2003 nr 169 poz. 1650ze zm.</p>
                <div className={classes.Decor}>
                    <span></span>
                    <span></span>
                </div>
            </div>
            <h1>W jednym dokumencie:</h1>
            {box}
            {redirectBox}
        </div>
    )
}

const mapStateToProps = state => {
    return {
        isAuth: state.firebase.auth.uid
    }
}

export default connect(mapStateToProps)(landingpage)