import React from 'react';

import classes from './riskMatric.module.css';

const riskMatric = props => {

    let value = props.effect+'|'+props.propability;
    let type = props.type;
    let risk;    

    switch (value) {
        case ("small|small"):
            if (type === 'td') {
                risk = <td className={[classes.Level,classes.ExtraSmall].join(' ')}>Bardzo mae</td>
            } else {
                risk = <div className={[classes.Level,classes.ExtraSmall].join(' ')}>Bardzo mae</div>};
            break;
        case ("small|medium"):
        case ("medium|small"):
            if (type === 'td') {
                risk = <td className={[classes.Level,classes.Small].join(' ')}>Mae</td>
            } else {
                risk = <div className={[classes.Level,classes.Small].join(' ')}>Mae</div>;}
            break;
        case ("small|big"):
        case ("medium|medium"):
        case ("big|small"):
            if (type === 'td') {
                risk = <td className={[classes.Level,classes.Medium].join(' ')}>rednie</td>
            } else {
                risk = <div className={[classes.Level,classes.Medium].join(' ')}>rednie</div>};
            break;
        case ("medium|big"):
        case ("big|medium"):
            if (type === 'td') {
                risk = <td className={[classes.Level,classes.Big].join(' ')}>Due</td>
            } else {
                risk = <div className={[classes.Level,classes.Big].join(' ')}>Due</div>};
            break;
        case ("big|big"):
            if (type === 'td') {
                risk = <td className={[classes.Level,classes.ExtraBig].join(' ')}>Bardzo due</td>
            } else {
                risk = <div className={[classes.Level,classes.ExtraBig].join(' ')}>Bardzo due</div>};
            break;
        default:
            risk = null;
    };

    return risk !== null ? risk : null;
};

export default riskMatric;